.heading .headingtext::before {
  content: " ";
  width: 55px;
  position: absolute;
  top: -50%;
  left: 100%;
  margin-left: -75px;
  border: 3.5px solid #FFD92A;
  border-radius: 10px;
}

.heading .headingtext1::before {
  content: " ";
  width: 55px;
  position: absolute;
  top: -50%;
  left: 100%;
  margin-left: -90px;
  border: 3.5px solid #FFD92A;
  border-radius: 10px;
}

.mission-heading .mission-headingtext::before {
  margin-left: -110px;
}

.headingtext,
.headingtext1,
.iqac-news-headingText {
  font-weight: bold;
  color: #B10F56 !important;
}

.outcomes-heading {
  color: #000;
  position: relative;
  /* display: inline-block; */
}

.outcomes-heading .outcomes-headingtext::before {
  content: " ";
  width: 120px;
  position: absolute;
  top: 110%;
  left: 45.5%;
  margin-top: 20px;
  /* margin-left: -210px; */
  border: 2px solid #B10F56;
}

.iqac-news-heading {
  position: relative;
  display: inline-block;
}

.iqac-news-heading .iqac-news-headingText::before {
  content: " ";
  width: 55px;
  position: absolute;
  top: -50%;
  left: 4%;
  border: 3.5px solid #FFD92A;
  border-radius: 10px;
}

