  position: absolute;
  top: -50%;
  left: 42%;
  border: 3px solid #FFD92A;
  border-radius: 10px;
}

.events-card {
  background-color: #FDF8F9;
  padding: 65px 45px;
}

.events-card-col {
  width: 30%;
