  background: linear-gradient(90deg, #B10F56 2.8%, #D00846 96.83%);
  background-clip: text;
  -webkit-text-fill-color: transparent;
  color: #fff;
  margin: 0 0 25px;
  font-weight: bold;
  font-size: 1.2em;
  font-style: normal;
  line-height: 1.4;
  text-rendering: optimizeSpeed;
}

.events-card-img {
