  color: #AF0C3E;
  line-height: 1;
  margin: 0 0 2px;
  font-style: normal;
}

.event-date {
  margin: 0;
  width: 45px;
  float: left;
  color: #9D9D9D;
  font-size: 13px;
  font-weight: 500;
  text-transform: uppercase;
  text-align: center;
}

.event-desc {
  color: #656565;
  font-weight: 500;
  font-size: 14px;
  line-height: 19px;
  padding-left: 25px;
}

.show-all {
  display: flex;
  gap: 0.5em;
  flex-wrap: wrap;
  align-items: center;
  align-items: center;
  justify-content: center;
}

.iqac-admission-container1 {
  padding: 60px 0;
  background: #af0c3e;
  background-image: -webkit-gradient(linear, left top, right top, from(#b10f56), to(#D00846));
  background-image: linear-gradient(to right, #b10f56, #D00846);
  text-align: center;
  color: #fff;
}

.iqac-admission-title span {
  display: block;
  font-size: 23px;
  line-height: 27px;
  font-weight: 700;
  text-transform: uppercase;
}

.btn-admission {
  height: auto;
  border-radius: 50px;
  padding: 3px 20px;
  box-shadow: none;
  font-size: 12px;
  background: linear-gradient(to left, transparent 50%, #fff 50%);
  background-size: 200% 100%;
  background-position: right bottom;
  cursor: pointer;
  transition: all 0.4s cubic-bezier(0.65, 0.05, 0.36, 1);
  border: 1px solid #fff !important;
  color: #fff;

  &:hover {
    background-position: left bottom;
    color: #AF0C3E;
  }
}

